import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import Map from '../../components/Map';
import Gnb from '../../components/Gnb';
import { TiLocationArrow } from "react-icons/ti";
import SearchBar from '../../components/SearchBar';

const { kakao } = window; //   kakao window    

function Home(props) {
    const { userLists } = props;
    const [ userPosition, setUserPosition ] = useState(null);

    useEffect(() => {
        goToUserPosition();
    }, [])

    const goToUserPosition = useCallback(() => {
        if (navigator.geolocation) { // geolocation 
        navigator.geolocation.getCurrentPosition(
            (pos) => {
                const lat = pos.coords.latitude;
                const lng = pos.coords.longitude;

                const locPosition = new kakao.maps.LatLng(lat, lng);
                
                setUserPosition(locPosition);
            }, 
            (error) => {errorLocation(error)})
        }else{
        alert("      .")
        }
    }, [])

    const errorLocation = useCallback((e) => {
        let print;
        switch(e.code) {
            case e.PERMISSION_DENIED: // 1
            print = "   Geolocation API     !"
            break;

            case e.POSITION_UNAVAILABLE: // 2
            print = "         !"
            break;

            case e.TIMEOUT: // 3
            print = "           !"
            break;

            case e.UNKNOWN_ERROR:
            print = "        !"
            break;
        }
        alert(print);
    }, []);

    return (
        <div className='Home'>
            <div className='contentsBoxWithGnb'>
                <Link to={'/search'}>
                    <SearchBar />
                </Link>
                <Map 
                    goalPosition={userPosition}
                    userLists={userLists} />
                <button className='userPositionBtn' onClick={goToUserPosition}>
                    <TiLocationArrow />
                </button>
            </div>
            <Gnb />
        </div>
    );
}

export default Home;
